
.g-rotate-bar {
    position: absolute;
    bottom: 30px;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 99999;
}
.g-rotate-bar .svg-icon {
    display: inline-block;
    width: 1.2em;
    height: 1.2em;
    margin: 0 2em;
}



